body,html{
	margin:0;
	padding:0;
	font-size:16px;
	font-family: Arial, Helvetica, sans-serif;
	background:#fff;
}
h1{
	font-size:25px;
	margin:0;
	padding:10px 0;
}
.container{
	width:50%;
	margin:5% auto;
}
.form .row{
	padding:10px 0;
	position:relative;
}
.form .row label{
	display:inline-block;
	padding-bottom:5px;
	margin-right: 20px;
}
.form .row input, .form .row select, .form .row textarea, .selectlist{
	width:100%;
	border-radius:3px;
	padding:10px;
	box-sizing:border-box;
	border:solid 2px rgba(0,0,0,0.2);
	font-size: 16px;
	font-family: Arial, Helvetica, sans-serif;
}
.form .row input:focus, .form .row textarea:focus{
	border:solid 2px blue;
}
.selectlist{
	border:solid 2px rgba(0,0,0,0.2);
	display:block;
	text-decoration:none;
	color:#000;
	position:relative;
}
.ddmenu{
	width:100%;
	top:72px;
	left:0px;
	background:#fff;
	margin:0;
	padding:0;
	z-index:1;
}
i {
  border: solid rgba(0,0,0,0.2);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}
.selectlist:focus i{
	border-color: blue;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  position:absolute;
  right:10px;
  top:12px;
}
.ddmenu a{
	display:block;
	border:solid 2px rgba(0,0,0,0.2);
	padding:8px;
	background:#fff;
	text-decoration:none;
}
button{
	border:solid 2px rgba(0,0,0,0.2);
	font-size:18px;
	padding:10px;
	border-radius:5px;
}
.submit{
	color:white;
	background:rgba(0,0,0,.8)
}
.focusable {
  outline: 0;
}

.focusable:focus {
  border:solid 2px blue;
}


.nonfocusable {
  background-color: #bbb;
  color: black;
}

.nonfocusable::before {
  content: 'Non-focusable Element';
}

.success{
	color:green;
}
.error{
	color:red;	
}
#form-submitted-msg{
	font-size:18px;
	font-weight: bold;
-webkit-transition: all 500ms linear;
transition: all 500ms linear;
}

.checkbox{
	width:15px;
	height:15px;
	border:solid 2px rgba(0,0,0,.5);
	position: relative;
	display: inline-block;
	margin-right:5px
}
.checked{
	border:solid 2px rgba(0,0,0,0.5);
}
.checked:after{
	content: '';
    display: block;
	width: 4px;
    height: 8px;
	border: solid rgba(0,0,0,0.5);
	border-width: 0 3px 3px 0;
	transform: rotate(45deg);
	margin-left:4px;
}
.radio{
	width:18px;
	height:18px;
	border-radius:50%;
	border:solid 2px rgba(0,0,0,.5);
	position: relative;
	display: inline-block;
	margin-right:5px
}
.selected:after{
	content: '';
    display: block;
	width: 12px;
	height: 12px;
	border-radius: 50%;
	background:rgba(0,0,0,.5);
	margin-left:3px;
	margin-top:3px;
}
.checkbox-container{
	display: flex;
}
.checkbox-container label, .btn-wrapper{
	width:145px;
	display: inline-block;
}
